/* eslint-disable */
<template>
    <div id="app">
        <div class="title-box">
            <div class="cyberpunk-text">医保稽核智能助手Alpha</div>
        </div>
        <leftPlace class="left-box" @subHistory="chatChoose" @addNewChat="addNewChat" :QAHistoryList="QAHistoryList" />
        <rightPlace class="right-box" />
        <Chat ref="chatRef" v-if="showChat" :setQAList="setQAList"/>
        <!-- <AnsCtxBox /> -->
    </div>
</template>

<script>
import Chat from './components/Chat.vue'
// import AnsCtxBox from './components/AnsCtxBox.vue';
import leftPlace from './components/leftPlace.vue';
import rightPlace from './components/rightPlace.vue';

export default {
    name: 'App',
    components: {
        Chat,
        // AnsCtxBox
        leftPlace,
        rightPlace
    },
    data() {
        return {
            showChat: true,
            setQAList: [],
            QAHistoryList:[],
        }
    },
    created() {
        this.getChatHistory()
    },
    methods: {
        async addNewChat() {
            if (this.$refs.chatRef.QAList.length === 0) {
                this.$message.error('目前会话已是新会话')
            } else {
                await this.getChatHistory()
                this.showChat = false
                this.setQAList = []
                this.$nextTick(()=>{
                    this.showChat = true
                })
            }
            
        },
        chatChoose(item) {
            this.setQAList = item.miaCnsvInfoPOS.map(item => {
                const _content = JSON.parse(item.content)
                console.log(_content);
                return {
                    ...item,
                    askCtx: item.question,
                    ansCtx:  _content.summary,
                    ..._content
                }
            })
        },
        getChatHistory() {
            this.$axios({
                url: '/mia/logQuery',
                method: 'post',
                data: {}
            }).then(res => {
                if (res.data.success) {
                    this.QAHistoryList = res.data.data
                } else {
                    this.$message.error('获取历史会话失败')
                }

            }).catch(err => {
                console.log(err);
                this.$message.error(err.message || '服务异常')
            })
        }
    }
}
</script>
