/* eslint-disable */
<template>
    <div class="chat-content">
        <div class="main-area" ref="mainRef">
            <!-- <div class="title-line">
                XXXXXX
            </div> -->
            <div class="answer-box" v-scroll-to-bottom id="answer-box" ref="answerRef">
                <div class="answer-item" v-if="QAList.length === 0 && processData.status != 'PROCESS'"
                    style="margin-top: 50px;">
                    <div class="item-content ans-ctx">
                        <div class="avatar">
                            <i class="el-icon-s-platform"></i>
                        </div>
                        <div class="ctx-box" style="padding: 12px 24px;">
                            <h2>你好，我是医保稽核智能助手</h2>
                            <p>作为一名优秀的医保稽核助理，我有非常丰富的医保稽核经验，能协助你完成相关稽核规则的生成和校验，并输出你所需要的病例数据,同时作为大模型的探索产物,我能进行一定程度的推理和计算，帮你答疑解惑。
                            </p>
                            <p>想知道我的能力如何？点这里快速上手！持续提问相关问题可以帮助优化问题结果哦.</p>
                            <p><b>你可以试着问我：</b></p>
                            <li>1.生成超标准收费规则住院期间进行阑尾炎手术次数超过10次?</li>
                            <li>2.生成重复收费规则住院期间一级护理与二级护理?</li>
                            <li>3.生成串项目收费规则血常规检查?</li>
                        </div>
                    </div>
                </div>

                <div class="answer-item" v-for="item in QAList" :key="item.chatId">
                    <div class="item-content ask-ctx">
                        <div class="avatar">
                            <i class="el-icon-s-custom"></i>
                        </div>
                        <div class="ctx-box">{{ item.askCtx }}</div>
                    </div>
                    <div class="item-content ans-ctx">
                        <div class="avatar">
                            <i class="el-icon-s-platform"></i>
                        </div>
                        <div class="ctx-box">{{ item.ansCtx }}
                            <div class="table-ctx-inner" v-if="item.replyData && item.replyData.length > 0">
                                <DynamicTable :headers="item.dataSchema" :listData="item.replyData" />
                            </div>
                        </div>
                    </div>
                    <!-- <div class="table-ctx" v-if="item.listData">
                        <DynamicTable :headers="item.headers" :listData="item.listData" />
                    </div> -->
                </div>
                <div class="answer-item" v-if="processData.status === 'PROCESS'">
                    <div class="item-content ask-ctx">
                        <div class="avatar">
                            <i class="el-icon-s-custom"></i>
                        </div>
                        <div class="ctx-box">{{ processData.askCtx }}</div>
                    </div>
                    <div class="item-content ans-ctx">
                        <div class="avatar">
                            <i class="el-icon-s-platform"></i>
                        </div>
                        <div class="ctx-box" v-loading="loading">{{ processData.ansCtx }}
                            <!-- <div class="table-ctx-inner" v-if="processData.replyData && processData.replyData.length > 0">
                                <DynamicTable :headers="processData.dataSchema" :listData="processData.replyData" />
                            </div> -->
                        </div>
                    </div>
                    <!-- <AnsCtxBox class="ctx-box" :answerCtx="markdown" /> -->
                    <!-- <div class="table-ctx"></div> -->
                </div>
                <div class="sugges-area"
                    v-if="QAList.length > 0 && processData.status !== 'PROCESS' && suggesList.length > 0">
                    <div class="sugges-title">你可以这样问我：</div>
                    <div class="sugges-list">
                        <div v-for="(item, index) in suggesList" class="sugges-item" :key="index" @click="subSgs(item)">
                            <i>{{ item }}</i>
                        </div>
                    </div>
                </div>
            </div>
            <AskBox class="ask-box" @subMsg="subMsg" :isAvailable="processData.status !== 'PROCESS'"></AskBox>
        </div>
    </div>
</template>

<script>
import { fetchEventSource } from '@microsoft/fetch-event-source';
import AskBox from './AskBox.vue'
import DynamicTable from './DynamicTable.vue';
import { deepClone } from '../utils'

export default {
    name: "Chat",
    components: { AskBox, DynamicTable },
    props: {
        setQAList: {
            type: Array,
            default: () => []
        }
    },
    data() {
        return {
            cnsvId: '',
            askCtx: 'rwer\nasfasdd\n中文大撒大撒啊实打实的\n打赏啊实打实打算s',
            markdown: '# Hello, Markdown!\n\nThis is a _sample_ of **Markdown** text.\n\n```javascript\nconsole.log("Hello, World!");\n```\n',
            QAList: [],
            submitCtx: '',
            suggesList: [],
            processData: {
                chatId: '',
                status: 'DONE',
                askCtx: '',
                ansCtx: '\n',
                dataSchema: [],
                replyData: []
            },
            loading: false,
        }
    },
    watch: {
        setQAList(val) {
            if (val && val.length > 0) {
                this.QAList = val
                this.cnsvId = val[0].cnsvId
                this.suggesList = val[val.length - 1].suggestList || []
            } else {
                this.QAList = []
                this.cnsvId = ''
            }
            this.scrollToBottom()
        },
        processData: {
            deep: true,
            handler() {
                this.scrollToBottom()
            }
        }
    },
    methods: {
        subMsg(ctx) {
            this.submitCtx = ctx
            this.sendMessage()
        },
        subSgs(ctx) {
            this.submitCtx = ctx
            this.sendMessage()
        },
        async sendMessage() {
            const _chatId = new Date().getTime().toString(16)

            this.processData.chatId = _chatId
            this.processData.askCtx = this.submitCtx
            this.processData.ansCtx = '\n'
            this.processData.status = 'PROCESS'
            this.processData.dataSchema = []
            this.processData.replyData = []

            this.loading = true

            this.$axios({
                url: '/mia/prompt',
                method: 'post',
                data: {
                    id: _chatId,
                    appToke: '123',
                    userId: 'zhangsan',
                    question: this.submitCtx,
                    cnsvId: this.cnsvId
                }
            }).then(res => {
                this.loading = false
                if (res.data.success) {
                    const _data = res.data.data
                    _data.cnsvId ? this.cnsvId = _data.cnsvId : ''
                    this.typeText(_data.summary || '\n')
                    this.suggesList = _data.suggestList || []
                    if (res.data.code === 200) {
                        this.processData.dataSchema = _data.dataSchema
                        this.processData.replyData = _data.replyData
                    }
                } else {
                    this.processData.status = "ERROR"
                    this.$message.error('服务异常')
                }
            }).catch(err => {
                this.loading = false
                // this.typeText(this.markdown)
                this.processData.status = "ERROR"
                console.log(err);
                this.$message.error(err.message || '服务异常')
            })

            // steam
            // try {
            //     await fetchEventSource(`url`, {
            //         body:JSON.stringify(this.processData.askCtx),
            //         onopen(response) {
            //             if (response.status === 200) {
            //                 return;
            //             }
            //             throw new Error(`Failed to send message. HTTP ${response.status} - ${response.statusText}`);
            //         },
            //         onclose() {
            //             throw new Error('Failed to send message. Server closed the connection unexpectedly.');
            //         },
            //         onerror(err) {
            //             throw err;
            //         },
            //         onmessage(eventMessage) {
            //             if (eventMessage.data === '[DONE]') {
            //                 console.log('done', eventMessage)
            //                 return;
            //             }
            //             if (eventMessage.event === 'result') {
            //                 const result = JSON.parse(eventMessage.data);
            //                 // handleMessageResult(result);
            //                 return;
            //             }
            //             if (eventMessage.event === 'error') {
            //                 console.log('event error', eventMessage);
            //                 return;
            //             }
            //             this.processData.ansCtx += JSON.parse(eventMessage.data)

            //         }
            //     })
            // } catch (error) {

            // }

        },
        typeText(text) {
            this.processData.ansCtx = ''
            let index = 0
            let interval = setInterval(() => {
                if (index < text.length) {
                    this.processData.ansCtx += text.charAt(index)
                    index++
                } else {
                    clearInterval(interval)

                    this.ansEnd()
                }
            }, 30)
        },
        ansEnd() {
            this.processData.status = 'DONE'
            this.QAList.push(deepClone(this.processData))
        },
        scrollToBottom() {
            console.log('scrollToBottom');
            this.$nextTick(() => {
                // 确保DOM已更新
                // console.log(this.$refs.answerRef.scrollIntoView);
                // this.$refs.answerRef.scrollIntoView({ behavior: 'smooth', block: 'end' });
                this.$refs.answerRef.scrollTop = this.$refs.answerRef.scrollHeight
            });
        }
    }
}
</script>

<style lang="less" scoped>
.chat-content {
    width: 100%;
    height: 100vh;

    .main-area {
        width: 1200px;
        height: 100vh;
        margin: 0 auto;
        position: relative;
        background-color: #f8f8f8;

        .answer-box {
            width: 1050px;
            margin: 0 100px 0 50px;
            padding: 48px 0 12px 0;
            box-sizing: border-box;
            height: calc(100vh - 220px);
            overflow-y: auto;
            scrollbar-width: none;
            /* Firefox */
            -ms-overflow-style: none;

            /* IE 和 Edge */
            &::-webkit-scrollbar {
                width: 0;
                /* 对于竖向滚动条 */
                height: 0;
                /* 对于横向滚动条 */
            }

            .answer-item {
                width: 100%;
                height: auto;
                margin-bottom: 10px;
                margin-top: 20px;

                .item-content {
                    display: flex;
                    justify-content: space-between;
                    align-items: flex-start;
                    margin-bottom: 7px;

                    .avatar {
                        margin: 0 7px 14px 7px;
                        width: 36px;
                        height: 36px;
                        border-radius: 5px;
                        text-align: center;
                        line-height: 36px;
                        background-color: #fff;
                        color: #46c791;
                    }

                    .ctx-box {
                        letter-spacing: 1px;
                        width: calc(100% - 50px);
                        min-width: calc(100% - 50px);
                        max-width: calc(100% - 50px);
                        white-space: pre-wrap;
                        line-height: 24px;
                        padding: 7px 24px;
                        box-sizing: border-box;
                    }
                }

                .ask-ctx {
                    margin-top: 35px;
                    margin-bottom: 1px;
                }

                .ans-ctx {
                    .avatar {
                        color: #5c87ec;
                    }

                    .ctx-box {

                        background-color: #fff;
                        border-radius: 15px;
                        -webkit-box-shadow: 0 16px 20px 0 rgba(174, 167, 223, .2);
                        box-shadow: 0 16px 20px 0 rgba(174, 167, 223, .2);

                        .table-ctx-inner {
                            width: 100%;
                            height: auto;
                            margin-top: 15px;
                            margin-bottom: 5px;

                        }

                        /deep/.el-table--scrollable-x .el-table__body-wrapper {
                            &::-webkit-scrollbar {
                                height: 8px;
                                cursor: pointer;
                                background-color: #78d3ad;
                            }
                        }
                    }
                }

                .table-ctx {
                    width: 100%;
                    height: auto;
                    box-sizing: border-box;
                    padding-left: 50px;
                }
            }

            .sugges-area {
                padding: 10px 0 10px 50px;
                box-sizing: border-box;
                color: #999;
                font-size: 14px;

                .sugges-item {
                    margin: 22px 0;

                    i {
                        cursor: pointer;
                        padding: 5px 10px;
                        border-radius: 5px;
                        background-color: #fff;

                        &:hover {
                            background-color: #46c791;
                            color: #fff;
                        }
                    }
                }
            }
        }

        .ask-box {
            width: 100%;
            height: 160px;
            position: absolute;
            bottom: 24px;
        }
    }
}</style>