<template>
    <div class="ask-box">
        <textarea ref="askInputRef" class="ask-input" v-model="askContent" placeholder="请输入问题, 换行使用Shift+Enter"
            @keydown.prevent.enter="handleTextareaKeydown">
                </textarea>
        <el-button class="send-btn" icon="el-icon-s-promotion" round type="primary" @click="submitMsg" :disabled="!isAvailable"></el-button>
    </div>
</template>

<script>
export default {
    props: {
        isAvailable: {
            type: Boolean,
            default: true
        }
    },
    data() {
        return {
            askContent: ''
        }
    },
    methods: {
        handleTextareaKeydown(event) {
            console.log(event)
            // 检查是否为Ctrl+Enter组合键
            if (event.key === 'Enter' && event.shiftKey) {
                // 阻止默认行为（如表单提交或换行）
                event.preventDefault();
                // 在textarea中手动插入换行符
                this.$refs.askInputRef.value += '\n';
                // 可以将光标位置移动到新行末尾，根据需要调整
                const { selectionStart, selectionEnd } = this.$refs.askInputRef;
                this.$refs.askInputRef.setSelectionRange(selectionStart + 1, selectionEnd + 1);
            } else if (event.key === 'Enter' && !event.shiftKey) {
                // 如果只是Enter键，则执行发送消息的逻辑
                if(this.isAvailable) {
                    this.submitMsg();
                }
            }
        },
        submitMsg() {
            // 获取textarea的内容并处理，比如发送到服务器
            const message = this.$refs.askInputRef.value.trim();
            if (message) {
                // console.log('发送消息:', message);
                this.$emit('subMsg', message)
                // 清空textarea
                this.$refs.askInputRef.value = '';
                this.askContent = ''
            }
        }
    }
}
</script>

<style lang="less" scoped>
.ask-box {
    // width: 100%;
    // height: 160px;
    // position: absolute;
    // bottom: 24px;

    .ask-input {
        width: calc(100% - 200px);
        height: 143px;
        margin: 0 100px;
        font-size: 18px;
        -webkit-box-shadow: 0 16px 20px 0 rgba(174, 167, 223, .2);
        box-shadow: 0 16px 20px 0 rgba(174, 167, 223, .2);
        padding: 10px 20px;
        box-sizing: border-box;
        border: 1px solid #91c3f8;
        border-radius: 15px;
        line-height: 24px;
        color: #555;
        letter-spacing: 1px;

        &:hover {
            border: 1px solid #007aff;
        }

        &:focus {
            outline: none;
            border: 1px solid #007aff;
        }

    }

    .send-btn {
        position: absolute;
        bottom: 25px;
        right: 108px;

        &:hover {
            bottom: 24px;
            right: 106px;
        }
    }

    /deep/.el-button.is-round {
        padding: 10px 19px;
    }

    /deep/.el-button.is-round:hover {
        padding: 12px 23px;
    }

    // .ask-input:hover {
    //     border: 1px solid #007aff;
    // }

    // .ask-input:focus {
