<template>
    <el-table :data="listData" border stripe size="mini" class="d-table" :header-row-class-name="'table-header'">
        <el-table-column v-for="(item, index) in headers" v-if="item.visible" :key="index" :prop="item.prop" :label="item.label"
            :min-width="item.minWidth || 90">
            <template slot="header">
                <template v-if="item.label.length < 6">
                    {{ item.label }}
                </template>
                <template v-else>
                    <el-tooltip class="item" effect="dark" :content="item.label" placement="top">
                        <span>{{ item.label.substr(0,4) + '...' }}</span>
                    </el-tooltip>
                </template>
            </template>
        </el-table-column>
    </el-table>
</template>

<script>
export default {
    name: 'Dtable',
    props: {
        listData: {
            type: Array,
            default: () => []
        },
        headers: {
            type: Array,
            default: () => []
        },
    }
}
</script>

<style lang="less" scoped>
.d-table{
    -webkit-box-shadow: 0 6px 10px 0 rgba(174, 167, 223, .2);
    box-shadow: 0 6px 10px 0 rgba(174, 167, 223, .2);
}
</style>

<style lang="less">
.table-header{
    .el-table__cell {
        background-color: #f8f8f8 !important;
    }
}
</style>